.content {
  border-radius: 20px;
  padding: 20px;
  background-color: rgb(237, 237, 243);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4196078431);
  display: flex;
  justify-content: space-between;
  gap: 20px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  max-width: 100%;
}

.dialogs {
  flex: 0 0 auto;
  max-width: 220px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
}